.nav_bar{
  height: 100vh;
  max-height: 100vh;
  /* background: url(https://www.trafalgar.com/real-word/wp-content/uploads/sites/3/2021/01/Tokyo-cherry-blossom-season-unsplash.jpg); */
  background: url(https://hospitality-on.com/sites/default/files/2019-02/kyoto.jpg);
  background-position: center center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
}

nav {
  /* border: 3px solid black; */
  display: flex;
  justify-content: space-around;
  text-align: center;
  align-items: center;
  min-height: 15vh;
}

nav h1{
  font-size: 50px;
  font-family: 'Merriweather', serif;
}

.nav_item {
  width: 40%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  list-style-type: none;
}

.nav_item li{
  font-size: 15px;
  text-decoration: none;
  font-family: 'Merriweather', serif;
}

.product_page{
  /* border: 3px solid black; */
  margin: 0px auto;
  width: 85%;
  height: 80%;
  overflow-y: scroll;
  text-align: center;
  display: flex;
  flex-wrap: wrap;
  background: rgba(0,0,0,0.8);
}

.product_page::-webkit-scrollbar{
  display: none;
}

.display_box {
  display: flex;
  justify-content: space-around;
  border: 3px solid white;
  width: 22%;
  height: 25%;
  margin: 10px auto;
}

.swiper-container {
  /* border: 3px solid black; */
  width: 900px;
  height: 500px;
  margin: 0px auto;
  margin-top: 50px;
  overflow: hidden;
  object-fit: cover;
  object-position: center;
  box-shadow: 10px 10px 5px black;
}

.swiper-wrapper {
  width: 400%;
}

.swiper-slide {
  /* border: 3px solid pink; */
  max-width: 100% !important;
  height: auto !important;
  overflow: hidden;
  margin: 0px !important;
  object-fit: cover;
  object-position: center;
}

.swiper-slide img {
  width: 900px;
  height: 100%;
}

.title_cover {
  position: relative;
  background: rgba(0,0,0,0.85);
  height: 50px;
  width: 100%;
  bottom: 50px;
}

.title_cover h1 {
  position: absolute;
  font-size: 18px;
  bottom: 15px;
  left: 10px;
  font-family: 'Merriweather', serif;
}

.swiper-button-prev,
.swiper-button-next {
  color: #Cf3416 !important;
}

.swiper-pagination-bullet.swiper-pagination-bullet-active {
  background: #Cf3416 !important;
}